          <div class="row">
            <div class="col-lg-4"><h2 class="mb-3 mt-4">Looking for Coworking Space or Private Offices in Turin?</h2>
              <p><strong>Come join the biggest community of digital and tech professionals in Europe. </strong></p>
              <p><span style="font-weight: 400;"><span style="vertical-align: inherit;"><span
                  style="vertical-align: inherit;">Talent Garden Agnelli Foundation is the headquarters of Talent Garden in Turin created in partnership with the Agnelli Foundation. </span><span
                  style="vertical-align: inherit;">Located 5 minutes from the historic center of Turin, Talent Garden Agnelli Foundation is a campus dedicated to education, innovation and networking for new digital businesses that stands in the historic home of Senator Agnelli thanks to the original and futuristic intervention of the studio of international design Carlo Ratti Associati. </span><span
                  style="vertical-align: inherit;">Offering 5,000 square meters of creative workspace, a fablab, event spaces and meeting rooms for startups and innovative companies.</span></span></span>
              </p></div>
            <div class="col-lg-7 ml-auto mt-lg-0 mt-5">
              <div class="row text-editor">
                <div class="col-sm-6 p-4 box"><h5>We are a Community, not just a space</h5>
                  <ul class="square list-unstyled pl-3">
                    <li style="font-weight: 400;"><span style="font-weight: 400;">The biggest European community of digital and tech innovators</span>
                    </li>
                    <li style="font-weight: 400;"><span
                        style="font-weight: 400;">Dedicated on-site Community Manager</span></li>
                    <li style="font-weight: 400;"><span style="font-weight: 400;">Matchmaking app to connect around the Community</span>
                    </li>
